<template>
  <div>
    <div  v-if="isShowPermissionTip" class="permissionTip" @click="goLinkPermissionTip" >👑 <span>{{ $t('permission_tip') }}</span></div>
    <div class="whatsAppNumbers send-message-background">
      <el-card
          class="box-card whatsapp-numbers"
          shadow="always"
          :body-style="{ padding: '10px 10px 0 10px' }"
      >
        <div class="text item">
          <el-row style="height: 30px">
            <el-col :span="6">
              <p
                  class="send-message-card-title whatsAppTitle titleText"
                  v-text="whatsAppNumbersText"
              ></p>

            </el-col>
          </el-row>
          <el-input
              class="textareaBack"
              :placeholder=this.placeholderNum
              size="medium"
              v-model="whatsAppNumbersSimple"
              clearable
              slot="reference"
              @focus="handleWhatsAppNumbersFocus"
              @blur="handleWhatsAppNumbersBlur"
              @change="handleWhatsAppNumbersChange"
          >
          </el-input>
          <div class="remindTitle">
            <p class="remindText" v-if="remindTitle==='normal'">{{ $t('country_code_t') }}</p>
            <p class="warnText" v-if="remindTitle==='contentCheck'"><i class="exclamation">!</i> {{this.$t('please_enter_please_t')}}
              <i style="fontWeight:bolder;fontStyle:normal">{{this.$t('please_enter_one_t')}}</i>{{this.$t('please_enter_number_t')}}</p>
            <p class="warnText" v-if="remindTitle==='formatCheck'"><i class="exclamation">!</i> {{this.checkTheEnteredText}}
              <el-tooltip placement="top">
                <div slot="content">
                  <i class="el-icon-close" style="color:rgba(255, 8, 8, 100);margin-right:3px"></i>
                  <span style="color:#dcdfe6">{{ incorrectPhoneNum }}</span>
                  <br/>
                  <i class="el-icon-check" style="color:rgba(79, 251, 106, 100);margin-right:3px"></i>
                  <span style="color:#FFFFFF">+8613119140503</span>
                </div>
                <!-- <el-button>Top center</el-button> -->
                <i class="el-icon-question" style="color:rgba(255, 5, 5, 100)"></i>
              </el-tooltip>
            </p>

          </div>
        </div>
      </el-card>
    </div>
    <div class="messageText">
      <el-card
          class="box-card message-text"
          shadow="always"
          :body-style="{ padding: '10px 10px 14px 10px' }"
      >
        <p class="send-message-card-title titleText" v-text="messageTextText"></p>
        <el-select
            class="send-message-text-select textSelect"
            :placeholder="this.$t('select_template_t')"
            size="small"
            :no-data-text="this.$t('no_data_t')"
            v-model="messageTextTemplateSimple"
            @change="handleMessageTextTemplateChange"
            clearable
        >
          <el-option
              v-for="(item,index) in messageTextTemplateOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
          >
            <template>
                <span @click="selectTemplate(item.label)">{{
                    item.label
                  }}</span>
              <i @click.stop.self="handleEdit(index)" class="el-icon-delete"></i>
            </template>
          </el-option>
        </el-select>
        <el-button
            v-text="saveAsTemplateText"
            @click="saveAsTemplateSimpleClick"
            class="send-message-save-template-btn"
            type="success"
            size="small"
        >
        </el-button>
        <el-input
            id="text"
            class="textareaBack textareaForbidResize"
            type="textarea"
            :autosize="{ minRows: 6, maxRows: 6 }"
            v-model="messageTextSimple"
            @change="handleMessageTextChange"
        >
        </el-input>
      </el-card>
    </div>

    <div class="sendingSettings">
      <el-card
          class="box-card sending-setting"
          shadow="always"
          :body-style="{ padding: '10px 10px 14px 10px' }"
      >
        <el-row>
          <el-col :span="6">
            <span
                class="send-message-card-title titleText"
                v-text="sendingSettingsText"
            ></span>
          </el-col>
          <el-col :span="16" style="margin: 1px auto">
            <span
                class="send-message-common-font"
                v-text="sendingSettingsNoticeText"
            ></span>
          </el-col>
        </el-row>
        <el-row style="margin-top: 23px">
          <el-col :span="6" style="margin-top: 7px">
            <span
                class="send-message-card-title"
                v-text="sendingIntervalText"
                style="color: #c5c5c5"
            ></span>
          </el-col>
          <el-col :span="13">
            <el-input-number
                class="numSelect"
                v-model="minNumSimple"
                :precision="0"
                size="small"
                @change="handleMinNumChange"
                :min="6"
                :max="300"
                label="描述文字"
            ></el-input-number>
            ~
            <el-input-number
                class="numSelect"
                v-model="maxNumSimple"
                :precision="0"
                size="small"
                @change="handleMaxNumChange"
                :min="10"
                :max="300"
                label="描述文字"
            ></el-input-number>
            <span
                class="send-message-common-font"
                v-text="secondsText"
                style="font-size: 14px; margin-left: 4px"
            ></span>
          </el-col>
          <el-col
              :span="5"
              class="common-success-btn"
              :style="{ textAlign: 'center' }"
          >
            <el-button
                v-if="stopFlagSimple === true"
                type="success"
                @click="sendClick"
                plain
                size="small"
            >{{ $t('send_t') }}
            </el-button>
            <el-button
                v-else-if="stopFlagSimple === false"
                disabled
                type="success"
               style="cursor: no-drop;"
                plain
                size="small"
            >{{ $t('sending_t') }}
            </el-button>
            <el-button
                v-else-if="stopFlagSimple === 'disabled'"
                disabled
                type="success"
               style="cursor: no-drop;"
                size="small"
                class="disableBtn"
            >{{ $t('send_t') }}
            </el-button>
          </el-col>
        </el-row>
      </el-card>
    </div>
  </div>
</template>

<script>
import {phoneNumFormatCheck, phoneNumContentCheck, phoneNumNormal} from '@/utils/phone-num-check-utils'
import {EXTENSION_NAME, LOGSTORE, PLACEHOLDER_NUM, WAPLUS_REMOVE_FLAG} from "@/utils/extension-util";

export default {
  name: "SendMessages",
  data() {
    return {
      whatsAppNumbersText: this.$t('whatsapp_numbers_t'),
      whatsAppNumbersSimple: "",
      messageTextText: this.$t('message_text_t'),
      messageTextTemplateSimple: "",
      messageTextTemplateOptions: [],
      saveAsTemplateText: this.$t('save_as_t'),
      messageTextSimple: "",
      sendingSettingsText: this.$t('sending_settings_t'),
      sendingSettingsNoticeText: this.$t('messages_will_t'),
      sendingIntervalText: this.$t('sending_interval_t'),
      minNumSimple: 6,
      maxNumSimple: 10,
      secondsText: this.$t('seconds_t'),
      stopFlagSimple: true,
      sendMessageSimple: "simple",
      remindTitle: 'normal',
      incorrectPhoneNum: '55555',
      safetyChecked: false,
      simpleChangeNum: 0,
      incorrectPhoneNumArr: [],
      contentArr: [],
      extensionName: EXTENSION_NAME,
      placeholderNum: PLACEHOLDER_NUM,
      waplus_remove_flag: WAPLUS_REMOVE_FLAG,
      million_leads_t: '',
      million_leads_link:'',
      checkTheEnteredText: this.$t('check_the_entered_t'),
      isShowPermissionTip:false,
      PermissionTipLink:""
    };
  },
  created: async function () {
    const _this = this;

    // 中台是否允许展示tip进行分流
    chrome.storage.local.get(['zbaseConfig'], res => {
      let tip1 = res.zbaseConfig?.data?.config?.find(item=>item.name === 'bulkShunt')?.params.tip
      let tip = tip1? tip1[_this.$i18n.locale]||{} : {}
      if(tip.show && ['ar','en','es','pt-PT','pt-BR'].indexOf(_this.$i18n.locale) !== -1){
        _this.isShowPermissionTip = true
        _this.PermissionTipLink = tip.url
      }
      // console.log("chrome.storage.local.ge",tip1, this.$i18n.locale,tip,_this.isShowPermissionTip)
    })


    chrome.storage.local.get(['whatsAppNumbersSimple', 'minNumSimple', 'maxNumSimple', 'messageTextTemplateSimple', 'remindTitle', 'incorrectPhoneNum', 'simpleChangeNum', 'messageTextTemplateOptions', 'messageTextSimple', 'stopFlagSimple', 'zbaseConfig'], function (res) {
      _this.whatsAppNumbersSimple = res.whatsAppNumbersSimple;
      _this.minNumSimple = res.minNumSimple || 6;
      _this.maxNumSimple = res.maxNumSimple || 10;
      _this.messageTextTemplateSimple = res.messageTextTemplateSimple;
      _this.remindTitle = res.remindTitle || 'normal'
      _this.incorrectPhoneNum = res.incorrectPhoneNum || ''
      _this.simpleChangeNum = res.simpleChangeNum || 0
      // if (res.zbaseConfig) {
      //   _this.set_zbase_million_leads(res.zbaseConfig)
      // }
      if (_this.remindTitle !== 'normal') {
        let remindBorder = document.querySelector('.textareaBack.el-input.el-input--medium.el-input--suffix>.el-input__inner')
        remindBorder.style.borderColor = 'rgba(255, 5, 5, 100)'
      }
      _this.messageTextTemplateOptions = res.messageTextTemplateOptions || [
        {
          value: _this.$t('help_there_t'),
          label: _this.$t('help_there_t'),
        },
      ];
      if (res.messageTextSimple === undefined) {
        _this.messageTextSimple = _this.$t('help_there_t');
      } else {
        _this.messageTextSimple = res.messageTextSimple;
      }

      if (res.stopFlagSimple === undefined) {
        _this.stopFlagSimple = true;
      } else {
        _this.stopFlagSimple = res.stopFlagSimple;
      }
      const sendData = {
        stopFlagSimple: _this.stopFlagSimple,
      };
      _this.$parent.sendDataPopToContent(sendData);
    });
  },

  methods: {
    goLinkPermissionTip() {
      window.open(this.PermissionTipLink);
    },
    // set_zbase_million_leads(zbaseConfig) {
    //   let data = zbaseConfig.data.config
    //   for (let i = 0; i < data.length; i++) {
    //     if(data[i].name === "右上角Leads链接"){
    //       if (this.$i18n.locale === 'zh') {
    //         this.million_leads_t = data[i]['params']['zh_leads_name']
    //       }else if(this.$i18n.locale === 'pt'){
    //         this.million_leads_t = this.$t('million_leads_t')
    //       } else {
    //         this.million_leads_t = data[i]['params']['en_leads_name']
    //       }
    //       this.million_leads_link = data[i]['params']['url']
    //     }
    //   }
    // },
    goLinkLeads() {
      window.open(this.million_leads_link+'?utm_source=sender_promote_'+this.extensionName);
    },
    handleWhatsAppNumbersFocus() {
      let remindBorder = document.querySelector('.textareaBack.el-input.el-input--medium.el-input--suffix>.el-input__inner')
      this.remindTitle = 'normal'
      remindBorder.style.borderColor = 'rgb(220, 223, 230)'
    },
    handleWhatsAppNumbersBlur(e) {
      // this.handleWhatsAppNumbersChange(e.target.value)
    },
    handleWhatsAppNumbersChange(value) {
      if (!value) {
        phoneNumContentCheck(value, this)
        this.$sendLog(301302, {s_content: [], s_incorrect_phone_num: [], s_status: 1})
      } else {
        phoneNumNormal(this)
        phoneNumFormatCheck(value, this)
        if (this.remindTitle !== 'normal') {
          this.$sendLog(301302, {
            s_content: this.contentArr,
            s_incorrect_phone_num: this.incorrectPhoneNumArr,
            s_status: 2
          })
        } else {
          this.$sendLog(301303)
        }
      }
      this.simpleChangeNum++
      chrome.storage.local.set({whatsAppNumbersSimple: value, simpleChangeNum: this.simpleChangeNum - 1});
    },

    handleEdit(index) {
      this.messageTextTemplateOptions.splice(index, 1)
      chrome.storage.local.set({
        messageTextTemplateOptions: this.messageTextTemplateOptions,
      });
    },

    handleMessageTextTemplateChange(value) {
      this.messageTextSimple = value;
      chrome.storage.local.set({
        messageTextSimple: this.messageTextSimple,
        messageTextTemplateSimple: this.messageTextTemplateSimple,
      });
    },
    selectTemplate(value) {
      this.messageTextSimple = value;
      chrome.storage.local.set({
        messageTextSimple: this.messageTextSimple,
        messageTextTemplateSimple: this.messageTextTemplateSimple,
      });

    },
    saveAsTemplateSimpleClick() {
      this.$sendLog(301201);
      if (this.messageTextSimple.replace(/\s/g, "").length) {
        let tmp = {
          value: this.messageTextSimple,
          label: this.messageTextSimple,
        };
        let flag = 0;
        for (let templates of this.messageTextTemplateOptions) {
          if (templates.value === tmp.value)
            flag = 1
        }
        if (flag) {
          this.$confirm("", this.$t('the_template_t'), {
            customClass: "emptyContentTemplate",
            showConfirmButton: false,
            showCancelButton: false,
            center: true,
            type: "warning"
          })
        } else {
          document.getElementsByClassName('send-message-save-template-btn')[0].style.setProperty('animation', 'none')
          document.getElementsByClassName('send-message-save-template-btn')[0].style.setProperty('-webkit-animation', 'none')
          setTimeout(function () {
            document.getElementsByClassName('send-message-save-template-btn')[0].style.setProperty('animation', 'mymove 1s')
            document.getElementsByClassName('send-message-save-template-btn')[0].style.setProperty('-webkit-animation', 'mymove 1s')
            const keyframes1 = '@keyframes mymove {50% {background: #37B64B;color: #FFFFFF}}'
            const keyframes2 = '@-webkit-keyframes mymove {50% {background: #37B64B;color: #FFFFFF}}'
            const style = document.createElement('style');
            style.type = 'text/css';
            style.innerHTML = keyframes1;
            document.getElementsByClassName('send-message-save-template-btn')[0].appendChild(style);
            style.innerHTML = keyframes2;
            document.getElementsByClassName('send-message-save-template-btn')[0].appendChild(style);
          }, 1)
          this.messageTextTemplateOptions.push(tmp);
        }
      }
      chrome.storage.local.set({
        messageTextTemplateOptions: this.messageTextTemplateOptions,
      });
    },

    handleMessageTextChange(value) {
      chrome.storage.local.set({messageTextSimple: value});
    },
    handleMinNumChange(value) {
      this.$nextTick(() => {
        if (value >= this.maxNumSimple) {
          this.minNumSimple = this.maxNumSimple;
        }
        chrome.storage.local.set({minNumSimple: this.minNumSimple});
      })
    },
    handleMaxNumChange(value) {
      this.$nextTick(() => {
        if (value <= this.minNumSimple) {
          this.maxNumSimple = this.minNumSimple;
        }
        chrome.storage.local.set({maxNumSimple: this.maxNumSimple});
      })
    },
    async sendClick() {
      if (!this.$parent.firstSendTime) {
        chrome.storage.local.set({
          firstSendTime: Math.round(new Date() / 1000),
        });
      }
      this.$sendLog(301202);
      this.$parent.updateIsShowContinueBtn(true);
      let content
      if (this.messageTextSimple) {
        content = this.messageTextSimple.trim()
      } else {
        content = this.messageTextSimple
      }
      if (!content) {
        this.$confirm("", this.$t('please_enter_a_t'), {
          customClass: "emptyContent",
          showConfirmButton: false,
          showCancelButton: false,
          center: true,
          type: "warning",
        });
      } else if (!this.whatsAppNumbersSimple) {
        phoneNumContentCheck(this.whatsAppNumbersSimple, this)
      } else {
        if (this.remindTitle === 'normal') {
          phoneNumNormal(this)
        } else {
          return false
        }
        let dailySendNums = this.$parent.dailySendNums;
        const {isReachSendLimit, showConfirmTitle} =
            this.$checkDailySendNums(dailySendNums);
        if (isReachSendLimit) {
          let tmp_title = ''
          if(showConfirmTitle.indexOf('First time') === 0){
            tmp_title = showConfirmTitle.replace('First time limit has been reached(50 messages)\n\n300 messages are available at ',this.$t('first_time_1_t'))
            tmp_title = tmp_title.replace('.',this.$t('first_time_2_t'))
          }else {
            tmp_title = showConfirmTitle.replace('300 messages are available at ',this.$t('second_time_1_t'))
            tmp_title = tmp_title.replace('.',this.$t('second_time_2_t'))
          }
          this.$confirm(tmp_title, "", {
            confirmButtonText: "OK",
            type: "warning",
            showClose: false,
            showCancelButton: false,
            customClass: "daily-msg-send-warning",
          });
          return;
        }
        this.$parent.sendMessageType = this.sendMessageSimple;
        // this.$sendLog(301303,{s_amended_count:this.simpleChangeNum})
        //从触发警示页开始记为触发send
        this.$sendLog(301304)
        this.simpleChangeNum = 0
        chrome.storage.local.remove(['simpleChangeNum', 'confirmSendData', 'confirmSetData'])
        await this.$parent.handleConfirmClick()
        const _this = this;
        chrome.storage.local.get(['isShowSafetyWarningFlagValue', 'dialogVisible', 'confirmSetData', 'confirmSendData'], (res) => {
          if (res.isShowSafetyWarningFlagValue || res.isShowSafetyWarningFlagValue == undefined) {
            _this.$parent.isShowSafetyWarningFlagValue = true
          } else {
            _this.$parent.activeName = "statistics"
            if (res.dialogVisible === false) {
              if (res.confirmSetData) {
                chrome.storage.local.set(res.confirmSetData)
              }
              if ((typeof res.confirmSendData === 'object') && (res.confirmSendData !== null)) {
                _this.$parent.sendDataPopToContent(res.confirmSendData)
              }
            } else {
              _this.$parent.dialogVisible = true
            }
          }
        })
      }
    },
  },
};
</script>


