<script>
import { getAllPhoneNumberList } from "@/utils/phone-number-util";
import {EXTENSION_NAME, WAPLUS_REMOVE_FLAG} from "@/utils/extension-util";
  export default {
    name:'GroupTools',
    props:['chooseWarning','beRemovedWarning',"groupOptions", "groupsMap"],
    data() {
        return {
            radiosExportType: 'xlsx',
            exportGroupInfo:'',
            excludeGroupAdminsValue: true,
            extensionName: EXTENSION_NAME,
            waplus_remove_flag: WAPLUS_REMOVE_FLAG,
            radiosChat: 'radioChatAll',
            wowButton: true,
            isShowPermissionTip:false,
            PermissionTipLink:''
        }
    },
    created() {
        const _this = this
        // 中台是否允许展示tip进行分流
        chrome.storage.local.get(['zbaseConfig'], res => {
          let tip1 = res.zbaseConfig?.data?.config?.find(item=>item.name === 'bulkShunt')?.params.tip
          let tip = tip1? tip1[_this.$i18n.locale]||{} : {}
          if(tip.show && ['ar','en','es','pt-PT','pt-BR'].indexOf(_this.$i18n.locale) !== -1){
            _this.isShowPermissionTip = true
            _this.PermissionTipLink = tip.url
          }
          // console.log("chrome.storage.local.ge",tip1, this.$i18n.locale,tip,_this.isShowPermissionTip)
        })


        chrome.storage.local.get(['zbaseConfig','groupOptions','groupsMap','exportGroupInfo','excludeGroupAdminsValue'],res=>{
          if (res.zbaseConfig && res.zbaseConfig.data.config) {
            res.zbaseConfig.data.config.forEach(item => {
              if (item.name === 'Drainage configuration' && item.params) {
                _this.wowButton = item.params.wowButton ? item.params.wowButton === '1' ? true : false : true
              }
            })
          }
          _this.groupOptions =
              res.groupOptions !== undefined ? res.groupOptions : [];
          _this.groupsMap = res.groupsMap || {};
          _this.exportGroupInfo = res.exportGroupInfo || '';
          _this.excludeGroupAdminsValue =
          res.excludeGroupAdminsValue !== undefined
          ? res.excludeGroupAdminsValue : true;
        })
    },

    methods: {
        goLinkPermissionTip() {
          window.open(this.PermissionTipLink);
        },
        wapiOpen () {
          window.open('https://chrome.google.com/webstore/detail/wapi-send-personalized-me/nmbjblpeeiobnakglbickhpgbinojbch?utm_source=easy_sender_option')
        },
        handleGroupChange(value) {
            let group = this.groupsMap[this.exportGroupInfo];
            chrome.storage.local.set({exportGroupInfo:this.exportGroupInfo})
            let phoneNumberList = getAllPhoneNumberList(group);
            this.$parent.messageBoxClose();
            if (phoneNumberList.length === 0) {
                console.log(('be removed out the group'))
            }
        },
        handleGroupChangeLog(value){
          if(value){
            this.$sendLog(303205);
          }
        },
        exportClick() {
            // if(this.chooseWarning || this.beRemovedWarning) return false
            this.$sendLog(303201,{s_status: this.radiosExportType === 'xlxs' ? 1 : this.radiosExportType === 'csv' ? 2 : this.radiosExportType === 'vcf' ? 3 : ''});
            let tmp = this.excludeGroupAdminsValue ? 'excludeAdmins' : 'all';
            const sendData = {exportGroup: tmp,exportGroupInfo:this.exportGroupInfo};
            this.$parent.sendDataPopToContent(sendData);
        },
        handleExcludeGroupAdminsValueChange(value) {
            console.log(value)
            this.$sendLog(303202);
            chrome.storage.local.set({excludeGroupAdminsValue: this.excludeGroupAdminsValue});
        },
        goLinkTutorial(link) {
            this.$sendLog(303203);
            window.open(link);
        },

        goLinkContact(link) {
            this.$sendLog(303204);
            window.open(link);
        }
    },
  }
</script>

