<template>
  <div>
    <div id="guidePage" v-if="isGuide">
      <div v-if="guideStep === 1">
        <img
            alt="guideStep1"
            src="pic/guide_1.png"
            style="width: 100%; height: 100%"
        />
        <button class="guideButton" @click="guideBtnClick(1)">NEXT STEP</button>
      </div>
      <div v-if="guideStep === 2">
        <img
            alt="guideStep2"
            src="pic/guide_2.png"
            style="width: 100%; height: 100%"
        />
        <button class="guideStep2Button" @click="guideBtnClick(2)">NEXT STEP</button>
      </div>
      <div v-if="guideStep === 3">
        <img
            alt="guideStep3"
            src="pic/guide_3.png"
            style="width: 100%; height: 100%"
        />
        <button class="guideSendButton" @click="guideBtnClick(3)" v-if="guideShowSend">Send</button>
        <button class="guideSendButton" v-if="!guideShowSend" style="cursor: not-allowed;">Sending...</button>
      </div>
    </div>
    <div id="app" v-if="!isGuide" style="background-color: #F9F9F9">
    <div class="hello">
<!--      <ZbasePopupOnPopup v-if="!waplus_remove_flag" :zbase-method="supportUs"-->
<!--                         :browserType="browserType"></ZbasePopupOnPopup>-->
      <TopInfo></TopInfo>
      <el-tabs
          v-model="activeName"
          @tab-click="handleClick"
          :class="headTab"
          stretch="true"
      >
        <el-tab-pane v-if="!waplus_remove_flag" name="send-messages">
          <el-tooltip v-if="$i18n.locale==='pt'" slot="label" class="item" effect="dark" :content="sendMessagesText" placement="top-start">
            <div>
              <span
                  slot="label"
                  style="color: white; padding-left: 16px;display: block;overflow: hidden;text-overflow: ellipsis"
                  v-text="sendMessagesText"
              ></span>
            </div>
          </el-tooltip>
          <span v-else
              slot="label"
              style="color: white; padding-left: 16px"
              v-text="sendMessagesText"
          ></span>
        </el-tab-pane>
        <el-tab-pane name="send-messages-pro">
          <el-tooltip v-if="$i18n.locale==='pt'" slot="label" class="item" effect="dark" :content="sendMessagesProText" placement="top-start">
            <div>
             <span
                 slot="label"
                 style="color: white; padding-left: 16px;display: block;overflow: hidden;text-overflow: ellipsis"
                 v-text="sendMessagesProText"
             >
              </span>
            </div>
          </el-tooltip>
          <span v-else
              slot="label"
              style="color: white; padding-left: 16px;"
              v-text="sendMessagesProText"
          >
              </span>
        </el-tab-pane>
        <el-tab-pane name="group-tools">
          <el-tooltip v-if="$i18n.locale==='pt'" slot="label" class="item" effect="dark" :content="exportGroupText" placement="top-start">
            <div>
              <span
                  slot="label"
                  style="color: white;display: block;overflow: hidden;text-overflow: ellipsis"
                  v-text="exportGroupText"
              ></span>
            </div>
          </el-tooltip>
          <span v-else
              slot="label"
              style="color: white;"
              v-text="exportGroupText"
          ></span>
        </el-tab-pane>
        <el-tab-pane name="statistics">
          <el-tooltip v-if="$i18n.locale==='pt'" slot="label" class="item" effect="dark" :content="statisticsText" placement="top-start">
            <div>
              <span
                  slot="label"
                  style="color: white;padding-right:16px;display: block;overflow: hidden;text-overflow: ellipsis"
                  v-text="statisticsText"
              ></span>
            </div>
          </el-tooltip>
          <span v-else
              slot="label"
              style="color: white;padding-right:16px;display: block;overflow: hidden;text-overflow: ellipsis"
              v-text="statisticsText"
          ></span>
        </el-tab-pane>
        <el-tab-pane name="more-tools">
          <el-tooltip v-if="$i18n.locale==='pt'" slot="label" class="item" effect="dark" :content="moreToolsText" placement="top-start">
            <div>
              <span
                  slot="label"
                  style="color: white;padding-right:16px;display: block;overflow: hidden;text-overflow: ellipsis"
                  v-text="moreToolsText"
              ></span>
            </div>
          </el-tooltip>
          <span v-else
              slot="label"
              style="color: white;padding-right:16px;display: block;overflow: hidden;text-overflow: ellipsis"
              v-text="moreToolsText"
          ></span>
        </el-tab-pane>
      </el-tabs>
      <div
          class="tabContainer"
          :class="activeName === 'send-messages' ? 'grey-background-color' : ''"
      >
        <SendMessages
            ref="messageSimple"
            v-if="
                activeName === 'send-messages'
                "
        ></SendMessages>

        <SendMessagesPro
            :groupsMap="groupsMap"
            :groupOptions="groupOptions"
            ref="messagePro"
            v-if="
                activeName === 'send-messages-pro'
                "
        ></SendMessagesPro>

        <GroupTools
            :chooseWarning="chooseWarning"
            :beRemovedWarning="beRemovedWarning"
            :groupsMap="groupsMap"
            :groupOptions="groupOptions"
            v-else-if="
                activeName === 'group-tools'
                "
        ></GroupTools>

        <Statistics
            :isShowContinueBtn="isShowContinueBtn"
            :disabledSendingFlag="disabledSendingFlag"
            :countAll="countAll"
            :countTotal="countTotal"
            :deduplicated="deduplicated"
            :countFail="countFail"
            :countSuccess="countSuccess"
            :waitSeconds="waitSeconds"
            v-else-if="
                activeName === 'statistics'
                "
        ></Statistics>
        <el-card
          shadow="always"
          :body-style="{ padding: '0px 0px 20px' }"
          style="margin: 11px 0px 10px; padding: 0 10px"
          v-else-if="activeName === 'more-tools'"
        >
          <moreToolsVue></moreToolsVue>
        </el-card>
        <!-- 确认发送弹窗 -->
        <el-dialog
            :visible.sync="dialogVisible"
            width="459px"
            top="125px"
            :modal-append-to-body="false"
            :show-close="true"
            :close-on-click-modal="false"
            @open="confirmSendDialogOpen"
            @close="confirmSendDialogClose"
            custom-class="sendConfirmPop"
            center
        >
          <div style="margin-bottom:23px;fontWeight:bold;fontStyle:normal;color:#0f0101">{{ this.$t('suggest_1_t') }}
          </div>
          <ul class="sendConfirmUl" style="margin:23px 0 35px 15px">
            <li><i class="boldFont">{{ this.$t('suggest_2_t') }}</i>
              <br>
              <span v-html="this.$t('suggest_3_t')"></span>
            </li>
            <li><i class="boldFont">{{ this.$t('suggest_4_t') }}</i>
              <br>
              <span v-html="this.$t('suggest_5_t')"></span>
            </li>
          </ul>
          <div><a href="#" @click="openWaHelpCenter">{{ this.$t('about_t') }}</a></div>
          <div class="safetyFooter" slot='footer'>
            <el-button type="success" @click="confirmSendDialog">{{ this.$t('agree_t') }}</el-button>
            <br>
            <el-checkbox style="margin-top:11px" v-model="notShowAgainModel" @change="notShowAgain">
              {{ this.$t('donot_t') }}
            </el-checkbox>
          </div>
        </el-dialog>

        <!-- 为空弹窗 -->
        <el-dialog
            :visible.sync='concatsIsNull'
            width="459px"
            top='220px'
            class='concatsPop'
            :modal-append-to-body='false'
            center>
          <div style="margin-bottom:55px;height:24px;line-height:24px">
            <i class="el-icon-warning" style="color:red;margin:0 16px;fontSize:24px;vertical-align:middle"></i>
            <span style="color:red">{{ this.$t('please_enter_at_t') }}</span>
          </div>
        </el-dialog>

        <!-- 安全警示页弹窗 -->
        <el-dialog
            :visible.sync="isShowSafetyWarningFlagValue"
            width="459px"
            top='120px'
            custom-class='safetyPop'
            :modal-append-to-body='false'
            :show-close='true'
            :close-on-click-modal="false"
            @open='openSafetyDialog'
            @close='closeSafetyDialog'
            center>
          <div slot="title" style="fontSize:18px;fontWeight:bold;fontStyle:normal;color:#0f0101">
            {{ this.$t('safety_1_t') }}
          </div>
          <div
              style="margin:-10px 0 0 30px;height:25px;line-height:25px;fontWeight:bold;fontStyle:normal;color:#0f0101">
            {{ this.$t('safety_2_t') }}
          </div>
          <ul class="safetyFirstUl">
            <li><span>{{ this.$t('safety_3_1_t') }}<i class="boldFont">≥ 15</i>{{ this.$t('safety_3_2_t') }}</span></li>
            <li><span>{{ this.$t('safety_4_1_t') }}<i class="boldFont">{{ this.$t('safety_4_2_t') }}</i></span></li>
            <li><span><i class="boldFont">{{ this.$t('safety_5_1_t') }}</i>{{ this.$t('safety_5_2_t') }}</span></li>
          </ul>
          <div>
            <div style="margin:19px 0 0 30px;height:25px;line-height:25px"><i
                class="boldFont">{{ this.$t('safety_6_t') }}</i></div>
            <ul class="safetySecondUl">
              <li><span>{{ this.$t('safety_7_t') }}<i class="boldFont">(≤24h)</i></span></li>
              <li><span>{{ this.$t('safety_8_t') }}<i class="boldFont">(>24h)</i></span></li>
            </ul>
          </div>
          <div class="safetyFooter" slot='footer'>
            <el-button type="success" @click="updateShowSafetyWarningFlag">{{ this.$t('agree_t') }}</el-button>
            <br>
            <el-checkbox style="margin-top:11px" v-model="isDontShowSafetyWarning" @change="againCheckBoxChange">
              {{ this.$t('donot_t') }}
            </el-checkbox>
          </div>
        </el-dialog>

      </div>
      <CommonBottomHref v-bind:sendType="activeName" v-bind:locale="locale"></CommonBottomHref>

      <!-- 引流弹框【其他语言】，是否显示：中台配置、语言为其他语言、频率1天1次 -->
      <AttractDialog v-if="isShowAttractDialog  && $i18n.locale !== 'ar' " :isShowAttractDialog="isShowAttractDialog"
                     :canClose="!zbaseConfig.enforce"   @close='closeAttractDialog' @goToAttractDialogLink="goToAttractDialogLink"></AttractDialog>

      <!-- 引流弹框【阿拉伯语】 -->
      <AttractDialogArabic v-if="isShowAttractDialog && $i18n.locale === 'ar'" :isShowAttractDialog="isShowAttractDialog"
                           :canClose="!zbaseConfig.enforce"  @close='closeAttractDialog' @goToAttractDialogLink="goToAttractDialogLink"></AttractDialogArabic>

    </div>
  </div>
  </div>
</template>

<script>
import XLSX from "xlsx";
import CommonBottomHref from "@/components/common//BottomHref";
import {getBrowser} from "@/utils/browser-util";

import TopInfo from "@/components/topInfo/TopInfo";
import SendMessages from "@/pages/sendMessages/SendMessages";
import SendMessagesPro from "@/pages/sendMessagesPro/SendMessagesPro";
import GroupTools from "@/pages/groupTools/GroupTools";
import Statistics from "@/pages/statistics/Statistics";
import {ZbasePopupOnPopup} from "zbase-popup-component";
import {WAPLUS_REMOVE_FLAG} from "@/utils/extension-util";
import AttractDialog from "@/components/common/AttractDialog";
import AttractDialogArabic from "@/components/common/AttractDialogArabic";
import moreToolsVue from '@/pages/moreTools/MoreTools.vue'

export default {
  name: "App",
  components: {
    CommonBottomHref,
    TopInfo,
    SendMessages,
    SendMessagesPro,
    GroupTools,
    Statistics,
    AttractDialog,
    AttractDialogArabic,
    moreToolsVue
    // ZbasePopupOnPopup
  },
  data() {
    return {
      guideShowSend: true,
      isGuide: false,
      guideStep: 1,
      headTab:this.$i18n.locale === 'pt' ? 'headerTab pt':'headerTab',
      concatsIsNull: false,
      isShowSafetyWarningFlagValue: false,
      isDontShowSafetyWarning: false,
      file: null,
      sendMessagesText: this.$t('send_messages_t'),
      sendMessagesProText: this.$t('send_messages_pro_t'),
      activeName: WAPLUS_REMOVE_FLAG ? "send-messages-pro" : "send-messages",
      locale:this.$i18n.locale,
      downloadTemplateText: "Excel Template",
      agreeAndContinue: "Agree & Continue",
      sendMessageType: "simple",
      sendMessageStatisticsType: "simple",
      sendSilentSendValue: false,
      groupOptions: [],
      groupsMap: {},
      chooseWarning: false,
      beRemovedWarning: false,
      statisticsText: this.$t('statistics_t'),
      exportGroupText: this.$t('group_tools_t'),
      sleepText: "Help",
      countSuccess: 0,
      countFail: 0,
      countAll: 0,
      countTotal: 0,
      deduplicated: 0,
      waitSeconds: 0,
      firstLoadValue: false,
      selected: "",
      dropdownMap: {
        "User Guide": "https://wawebsender.com/sender-help",
        "Contact Us":
            "https://api.whatsapp.com/send/?phone=%2B8617891209984&text=Hi+WAPlus+Sender%21+I+have+a+question.",
      },
      dialogVisible: false,
      dailySendNums: 0,
      phoneNumList: [],
      isShowContinueBtn: false,
      loadingPage: null,
      disabledSendingFlag: true,
      firstSendTime: "",
      browserType: "Chrome",
      notShowAgainModel: false,
      waplus_remove_flag: WAPLUS_REMOVE_FLAG,
      isShowAttractDialog: false,
      zbaseConfig:{
        enforce:false,
        attractDialogLink:"",
      },
      moreToolsText: this.$t('more_tools_t'),
    };
  },
  methods: {
    closeAttractDialog(){
      this.isShowAttractDialog = false;
    },
    goToAttractDialogLink(){
      window.open(this.zbaseConfig.attractDialogLink);
    },
    guideBtnClick(step) {
      if (step === 1) {
        this.guideStep = 2
        this.$sendLog(306201)
      } else if (step === 2) {
        this.guideStep = 3
        this.$sendLog(306202)
      } else {
        this.guideClose()
        this.$sendLog(306203)
      }
    },
    guideClose() {
      this.guideShowSend = false
      this.sendPrivateDomain()
    },
    sendPrivateDomain() {
      chrome.tabs.query({ active: true, currentWindow: true }, function (tabs) {
        chrome.tabs.sendMessage(tabs[0].id, { sendPrivateDomain: true })
      })
    },
    openWaHelpCenter() {
      window.open('https://faq.whatsapp.com/general/download-and-installation/about-linked-devices')
    },
    confirmSendDialogOpen() {
      chrome.storage.local.set({sendMessageFlag: false})
      let _This = this
      chrome.storage.local.get('confirmSendData', function (res) {
        let type = res['confirmSendData']['sendMessageType']
        if (type === 'simple') {
          _This.$sendLog(300206, {s_type: 1})
        }
        if (type === 'pro') {
          _This.$sendLog(300206, {s_type: 2})
        }
      })
    },
    confirmSendDialogClose() {
      let _This = this
      chrome.storage.local.get('confirmSendData', function (res) {
        let type = res['confirmSendData']['sendMessageType']
        if (type === 'simple') {
          _This.$sendLog(300207, {s_type: 1})
        }
        if (type === 'pro') {
          _This.$sendLog(300207, {s_type: 2})
        }
      })
    },
    confirmSendDialog() {
      this.dialogVisible = false
      if (this.notShowAgainModel) {
        chrome.storage.local.set({dialogVisible: false});
      }
      const _this = this
      chrome.storage.local.get(['confirmSendData', 'allDataImg', 'allDataVideo', 'allDataDocument', 'confirmSetData'], res => {
        if (res.confirmSetData) {
          chrome.storage.local.set(res.confirmSetData)
        }
        if ((typeof res.confirmSendData === 'object') && (res.confirmSendData !== null)) {
          let confirmSendData = res.confirmSendData;
          confirmSendData.allDataImg = res.allDataImg ? res.allDataImg : [];
          confirmSendData.allDataVideo = res.allDataVideo ? res.allDataVideo : [];
          confirmSendData.allDataDocument = res.allDataDocument ? res.allDataDocument : [];
          let type = res['confirmSendData']['sendMessageType']
          if (type === 'simple') {
            _this.$sendLog(300208, {s_type: 1})
          }
          if (type === 'pro') {
            _this.$sendLog(300208, {s_type: 2})
          }
          _this.sendDataPopToContent(confirmSendData)
        }
      })
      chrome.storage.local.set({sendMessageFlag: true})
    },
    handleConfirmClick() {
      this.$nextTick(() => {
        this.sendMessageStatisticsType = this.sendMessageType;
        let content = "";
        let phoneNum = "";
        let mediaType = "";
        let minNum = "";
        let maxNum = "";
        let sentSilent = "";
        let uploadExcel = "";
        let stopFlag = "";
        let stopFlagSimple = "";
        let groupConcats = "";
        const _this = this;
        if (this.sendMessageType === "pro") {
          content = this.$refs.messagePro.messageText;
          phoneNum = this.$refs.messagePro.whatsAppNumbers;
          if (this.$refs.messagePro.radio === "Image") {
            mediaType = "img";
          }
          if (this.$refs.messagePro.radio === "Video") {
            mediaType = "video";
          }
          if (this.$refs.messagePro.radio === "Document") {
            mediaType = "doc";
          }
          if (this.$refs.messagePro.radio === "Contact") {
            mediaType = "contact";
          }
          minNum = this.$refs.messagePro.minNum;
          maxNum = this.$refs.messagePro.maxNum;
          sentSilent = this.sendSilentSendValue;
          uploadExcel = this.$refs.messagePro.uploadExcelValue;
          groupConcats = this.$refs.messagePro.groupConcats;
          stopFlag = false;
          stopFlagSimple = "disabled";
        }
        if (this.sendMessageType === "simple") {
          content = this.$refs.messageSimple.messageTextSimple;
          phoneNum = this.$refs.messageSimple.whatsAppNumbersSimple;
          minNum = this.$refs.messageSimple.minNumSimple;
          maxNum = this.$refs.messageSimple.maxNumSimple;
          stopFlag = "disabled";
          stopFlagSimple = false;
        }
        this.countAll = 0;
        this.countTotal = 0;
        this.deduplicated = 0;
        this.countSuccess = 0;
        this.countFail = 0;
        this.waitSeconds = 0;
        //保存要发送的数据，因为在statistics获取不到pro或者simple模块数据
        const confirmSetData = {
          stopFlag,
          stopFlagSimple,
          sendMessageType: _this.sendMessageType,
          countAll: 0,
          countTotal: 0,
          deduplicated: 0,
          countSuccess: 0,
          countFail: 0,
          activeName: "statistics",
          phoneNumList: [],
          phoneNumSuccessList: [],
          phoneNumFailList: [],
          sendMessageStatisticsType: _this.sendMessageStatisticsType,
          waitSeconds: 0,
        }
        const sendData = {
          phoneNum,
          content,
          mediaType,
          minNum,
          maxNum,
          sentSilent,
          stopFlag,
          stopFlagSimple,
          uploadExcel,
          groupConcats,
          sendMessageType: this.sendMessageType,
          dailySendNums: this.dailySendNums,
        };
        chrome.storage.local.set({
          confirmSendData: sendData,
          confirmSetData: confirmSetData
        })
      });
    },
    notShowAgain(value) {
      let s_status = 1
      if (!value) {
        s_status = 2
      }
      let _This = this
      chrome.storage.local.get('confirmSendData', function (res) {
        let type = res['confirmSendData']['sendMessageType']
        if (type === 'simple') {
          _This.$sendLog(300209, {s_type: 1, s_status})
        }
        if (type === 'pro') {
          _This.$sendLog(300209, {s_type: 2, s_status})
        }
      })
    },
    fnExport(data) {
      const ws = XLSX.utils.json_to_sheet(data);
      ws["!cols"] = [
        {wch: 20},
        {wch: 20},
        {wch: 20},
        {wch: 20},
        {wch: 20},
        {wch: 20},
        {wch: 20},
      ];
      const wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws, "sheet1");
      XLSX.writeFile(wb, "Send Result.xlsx");
    },
    handleClick(tab, event) {
      if (this.activeName === "send-messages") {
        this.$sendLog(301101);
      }
      if (this.activeName === "send-messages-pro") {
        this.$sendLog(302101);
      }
      if (this.activeName === "group-tools") {
        this.$sendLog(303101);
      }
      if (this.activeName === "statistics") {
        this.$sendLog(304101);
      }
      if (tab.index === "0") {
        this.sendMessageType = "simple";
        chrome.storage.local.set({sendMessageType: this.sendMessageType});
      } else if (tab.index === "1") {
        this.sendMessageType = "pro";
        chrome.storage.local.set({sendMessageType: this.sendMessageType});
      }
      if (tab.index === "4") {
        return;
      } else {
        chrome.storage.local.set({activeName: this.activeName});
      }
    },
    sendDataPopToContent(sendData) {
      let userPhoneNum = ''
      chrome.storage.local.get(['urlStageFlag', 'userPhoneNum'])
          .then((res) => {
            if (res.urlStageFlag) {
              chrome.storage.local.remove('urlStageFlag')
              chrome.runtime.sendMessage({urlStageFlag: true, userPhoneNum: userPhoneNum})
            }
          })
      chrome.tabs.query({active: true, currentWindow: true}, function (tabs) {
        chrome.tabs.sendMessage(tabs[0].id, sendData);
      });
    },
    updateShowSafetyWarningFlag() {
      this.isShowSafetyWarningFlagValue = false;
      this.activeName = "statistics";
      let _This = this
      chrome.storage.local.get('confirmSendData', function (res) {
        let type = res['confirmSendData']['sendMessageType']
        if (type === 'simple') {
          _This.$sendLog(300204, {s_type: 1})
        }
        if (type === 'pro') {
          _This.$sendLog(300204, {s_type: 2})
        }
      })
      if (this.isDontShowSafetyWarning) {
        chrome.storage.local.set({isShowSafetyWarningFlagValue: false});
      } else {
        chrome.storage.local.set({isShowSafetyWarningFlagValue: true});
      }
      chrome.storage.local.get(['dialogVisible', 'confirmSetData', 'confirmSendData', 'allDataImg', 'allDataVideo', 'allDataDocument'], res => {
        if (res.dialogVisible !== false) {
          this.dialogVisible = true
        } else {
          if (res.confirmSetData) {
            chrome.storage.local.set(res.confirmSetData)
          }
          if ((typeof res.confirmSendData === 'object') && (res.confirmSendData !== null)) {
            let confirmSendData = res.confirmSendData;
            confirmSendData.allDataImg = res.allDataImg ? res.allDataImg : [];
            confirmSendData.allDataVideo = res.allDataVideo ? res.allDataVideo : [];
            confirmSendData.allDataDocument = res.allDataDocument ? res.allDataDocument : [];
            this.sendDataPopToContent(confirmSendData)
          }
        }
      })
    },
    sleep(s, e, l) {
      let m = 0;
      if (e >= s) {
        m = Math.floor(Math.random() * (e - s + 1) + s) * l;
      }
      return new Promise((t) => setTimeout(t, m));
    },
    openOrCheckWaTab(event) {
      chrome.tabs.query({currentWindow: true}, function (tabs) {
        if (tabs.length > 0) {
          const urlArr = tabs.filter((item) => item.url);
          let whatsappTab = urlArr.find(
              (e) =>
                  e.url.startsWith("http://web.whatsapp.com/") ||
                  e.url.startsWith("https://web.whatsapp.com/")
          );
          if (typeof whatsappTab !== "undefined") {
            chrome.tabs.highlight(
                {tabs: whatsappTab.index, windowId: whatsappTab.windowId},
                function () {
                  chrome.windows.update(whatsappTab.windowId, {focused: true});
                }
            );
            if (event === "reload") {
              chrome.tabs.reload(whatsappTab.id);
            }
          } else {
            window.open("https://web.whatsapp.com/");
          }
        }
      });
    },

    messageBoxClose() {
      try {
        this.$msgbox.close();
      } catch (err) {
      }
    },
    updateIsShowContinueBtn(isShow) {
      this.isShowContinueBtn = isShow;
