<template>
  <ZbaseCustomPopupDialog :functionMap="functionMap">
    <App/>
  </ZbaseCustomPopupDialog>
</template>

<script>
import App from '@/popup/App'
export default {
  name: "Main",
  components:{App},
  data() {
    return {
      functionMap: {
        joinGroup: function(event, vnode) {
          // 给content发送消息
          return new Promise((resolve)=> {
            chrome.tabs.query({active:true, currentWindow:true}, (tabs)=> {
              chrome.tabs.sendMessage(tabs[0].id, {
                id: 'joinGroup',
                from: 'popup',
                message: {
                  url: vnode.src
                }
              }, resolve)
            })
          })
        }
      }
    }
  }
}
</script>
