    uploadExcelClick(file, fileList) {
      if (fileList.length > 1) {
        fileList.splice(0, 1)
      }
      this.readExcel(file)
          .then((item) => {
            if (item && item.length > 0) {
              if (item[0] && item[0].sheet && item[0].sheet.length) {
                // 获取excel第一列
                let rows = Object.values(item[0].rows);
                let rowsTmp = [];
                for(let row of rows){
                  row = row.replace(/（/g, '(').replace(/）/g, ')')
                  if(row.match(/[^\s]+/g).join('').replace('(','').replace(')','').replace('）','').replace('（','').toLocaleLowerCase() === 'whatsappnumberwithcountrycode'){
                    row = 'WhatsApp Number';
                  }
                  // 不包含tips列
                  if(row !== 'Tips'){
                    rowsTmp.push(row)
                  }
                }
                this.excelRows = rowsTmp
                let excelData = item[0].sheet;
                excelData = this.dealExcelData(excelData,rowsTmp);
                this.excelNumbersText = excelData.length;
                //   chrome.storage.local.set({ excelData: excelData });
                this.uploadExcelValue = false;
                chrome.storage.local.set({excelData: excelData, uploadExcelValue: false, excelRows: rowsTmp});
                if (excelData.length === 0) {
                  this.$sendLog(302302, {s_status: 2});
                } else {
                  this.$sendLog(302302, {s_status: 1});
                }
              } else {
                this.excelNumbersText = 0
                this.uploadExcelValue = false
                chrome.storage.local.set({excelData: [], uploadExcelValue: false, excelRows:[]});
              }
            }
          })
          .catch((error) => {
            console.log(error);
          });
    },
    readExcel(file) {
      return new Promise(function (resolve, reject) {
        const reader = new FileReader();
        reader.onload = function (e) {
          const data = e.target.result;
          this.wb = XLSX.read(data, {
            type: "binary"
          });
          const result = [];
          this.wb.SheetNames.forEach((sheetName) => {
            result.push({
              sheetName: sheetName,
              sheet: XLSX.utils.sheet_to_json(this.wb.Sheets[sheetName]),
              rows: XLSX.utils.sheet_to_json(this.wb.Sheets[sheetName],{range: -1})[0]
            });
          });
          resolve(result);
        };
        reader.readAsBinaryString(file.raw);
      });
    },
    dealExcelData(excelData,rowsTmp) {
      let result = [];
      for (let i = 0; i < excelData.length; i++) {
        let data = excelData[i];
        // 处理一行excel数据
        data = this.checkSpecialCharacters(data,rowsTmp);
        // data = this.checkSpecialCharacters(dataTmp, data, "First Name");
        // data = this.checkSpecialCharacters(dataTmp, data, "Last Name");
        // data = this.checkSpecialCharacters(dataTmp, data, "Other");
        //只有在有whatsAppNumber且至少包含一个数字的时候才能认为是电话号码
        // if (whatsAppNumber) {
        //   whatsAppNumber = whatsAppNumber.toString()
        //   if (whatsAppNumber.match(/.*[0-9]/gm)) {
        //     let tmp = {};
        //     tmp["whatsAppNumber"] = whatsAppNumber;
        //     tmp["firstName"] = firstName;
        //     tmp["lastName"] = lastName;
        //     tmp["other"] = other;
        //
        //   }
        // }
        if(data['WhatsApp Number']){
          result.push(data);
        }
      }
      return result;
    },
