      this.insert(text)
    }
  },
  created: async function () {
    const _this = this;
    // 中台是否允许展示tip进行分流
    chrome.storage.local.get(['zbaseConfig'], res => {
      let tip1 = res.zbaseConfig?.data?.config?.find(item=>item.name === 'bulkShunt')?.params.tip
      let tip = tip1? tip1[_this.$i18n.locale]||{} : {}
      if(tip.show && ['ar','en','es','pt-PT','pt-BR'].indexOf(this.$i18n.locale) !== -1){
        _this.isShowPermissionTip = true
        _this.PermissionTipLink = tip.url
      }
      // console.log("chrome.storage.local.ge",tip1, this.$i18n.locale,tip,_this.isShowPermissionTip)
    })

    chrome.storage.local.get(['zbaseConfig','whatsAppNumbers', 'messageText', 'minNum', 'maxNum', 'sendAttachmentsValue', 'radio', 'remindTitlePro', 'incorrectPhoneNumPro', 'inputPhoneNum', 'inputPhoneNumFlag', 'groupMembersNum', 'groupConcats', 'proChangeNum', 'attachmentDivHtml', 'stopFlag', 'uploadExcelValue', 'excelData', 'messageTextTemplateOptions', 'messageTextTemplate', 'apologizeForBotherValue', 'groupOptions', 'groupsMap', 'groupInfo','excelRows'], (res) => {
      if (res.zbaseConfig && res.zbaseConfig.data.config) {
        res.zbaseConfig.data.config.forEach(item => {
          if (item.name === 'Drainage configuration' && item.params) {
            _this.wowButton = item.params.wowButton ? item.params.wowButton === '1' ? true : false : true
          }
        })
      }
      _this.whatsAppNumbers = res.whatsAppNumbers;
      _this.messageText = res.messageText || '';
      _this.minNum = res.minNum ? res.minNum : 6;
      _this.maxNum = res.maxNum ? res.maxNum : 10;
      _this.sendAttachmentsValue = res.sendAttachmentsValue;
      _this.radio = res.radio ? res.radio : '';
      _this.remindTitlePro = res.remindTitlePro || 'normal'
      _this.incorrectPhoneNumPro = res.incorrectPhoneNumPro || ''
      _this.inputPhoneNum = res.inputPhoneNum || 0
      _this.inputPhoneNumFlag = res.inputPhoneNumFlag || 'normal'
      _this.groupMembersNum = res.groupMembersNum || 0
      _this.groupConcats = res.groupConcats || 'normal'
      _this.proChangeNum = res.proChangeNum || 0
      _this.attachmentDivHtml = res.attachmentDivHtml ? res.attachmentDivHtml : ''
      if (_this.remindTitlePro !== 'normal') {
        let remindBorder = document.querySelector('.textareaBackPro.el-input.el-input--medium.el-input--suffix>.el-input__inner')
        remindBorder.style.borderColor = 'rgba(255, 5, 5, 100)'
      }
      if (res.stopFlag === undefined) {
        _this.stopFlag = true;
      } else {
        _this.stopFlag = res.stopFlag;
      }

      if (res.uploadExcelValue === undefined) {
        _this.uploadExcelValue = true;
      } else {
        _this.uploadExcelValue = res.uploadExcelValue;
      }
      const sendData = {
        stopFlag: _this.stopFlag,
      };
      _this.$parent.sendDataPopToContent(sendData);

      _this.excelNumbersText = res.excelData
