<script>
import {
  FACEBOOK_SHARE_URL, LINKEDIN_SHARE_URL, PINTEREST_SHARE_URL, Tool_URL, TUMBLR_SHARE_URL, TWITTER_SHARE_URL,
  WAPLUS_SENDER_REVIEWS_URL,
  WHATSAPP_SHARE_URL
} from "../../utils/common/config";
import {WAPLUS_REMOVE_FLAG} from "@/utils/extension-util";

export default {
  props: {
    sendType: {
      type: String,
      default: ''
    },
    locale: {
      type: String,
      default: ''
    }
  },
  name: 'CommonBottomHref',
  data() {
    return {
      waplus_remove_flag: WAPLUS_REMOVE_FLAG
    };
  },
  mounted() {
  },
  methods: {
    whatsapp() {
      //日志306201
      this.$sendLog(306201);
      window.open(WHATSAPP_SHARE_URL)
    },
    howToUse() {
      //日志305201
      this.$sendLog(305201);
      window.open('https://wawebsender.com/sender-help');
    },
    leaveReview(){
      this.$sendLog(305204);
      window.open(WAPLUS_SENDER_REVIEWS_URL);
    },
    support() {
      const _this = this
      this.$sendLog(305203);
      chrome.tabs.query({active: true, currentWindow: true}, function (tabs) {
        chrome.storage.local.get(['zbaseConfig'], res => {
          let servicePhoneConf = res.zbaseConfig?.data?.config?.find(item=>item.name === 'servicePhone')?.params?.phone ?? '+8617891209984'
          chrome.tabs.sendMessage(tabs[0].id, {openChat: servicePhoneConf });
        })
      })
    },
    moreTool(){
      this.$sendLog(305205);
      window.open(Tool_URL);
    },
    faq(){
      this.$sendLog(305206);
      window.open('https://wawebsender.com/whatsapp-sender#FAQ')
    }
  }
}
