<template>
  <div>
      <el-row :style="{height: '45px',marginBottom:'-2px'}">
        <el-col :span="8">
          <p class="waplus-title"
             :style="{fontStyle: 'normal', fontSize: '18px', fontWeight: 'bold', color: '#37B64A', textAlign: 'left',}">
            {{ waPlusSenderText }}</p>
        </el-col>
        <el-col :span="8" style="border:1px solid transparent">
         <ZbaseTopAdv  v-if="TopRotation" :browserType="browserType"></ZbaseTopAdv>
        </el-col>
        <el-col :span="8" class="topText">
          <el-header class="version-phone-font" v-text="versionText"></el-header>
          <el-footer class="version-phone-font" v-text="numberText" ></el-footer>
        </el-col>
      </el-row>
  </div>
</template>
<script>
import config from '../../../package.json'
import {ZbaseTopAdv} from "zbase-popup-component";
import {getBrowser} from "@/utils/browser-util";
import {TOP_NAME, WAPLUS_REMOVE_FLAG} from "@/utils/extension-util";
  export default {
    name:'TopInfo',
    data() {
        return {
            waPlusSenderText: TOP_NAME,
            versionText: 'Version: ',
            numberText: 'Number: + null',
            browserType: "Chrome",
            waplus_remove_flag: WAPLUS_REMOVE_FLAG,
            TopRotation: true,
        }
    },
    components: {
      ZbaseTopAdv,
    },
    created:async function() {
        this.browserType = getBrowser()['browser']
        const _this = this
        // this.versionText +=  chrome.runtime.getManifest().version
        this.versionText += config.version
        chrome.storage.local.get(['zbaseConfig','userPhoneNum'],res=>{
          if (res.zbaseConfig && res.zbaseConfig.data.config) {
            res.zbaseConfig.data.config.forEach(item => {
              if (item.name === 'Drainage configuration' && item.params) {
                _this.TopRotation = item.params.TopRotation ? item.params.TopRotation === '1' ? true : false : true
              }
            })
          }
          if(res.userPhoneNum){
              _this.numberText = 'Number: +' + res.userPhoneNum;
          }
        })
    },
    methods: {
    },
  }
</script>

<style scoped>
.waplus-title {
    margin: 7px auto 7px 12px;
}
.version-phone-font {
  text-align: right;
  font-size: 12px;
  padding: 0;
  /* background-color: #FFFFFF; */
  height: 17.5px !important;
  color: rgba(96, 98, 102, 100);
}
</style>
