<template>
  <div class="more-tools">
    <ul>
      <li v-for="item in moreToolsData" :key="item.id">
        <img class="tools-img" :src="item.iconUrl" alt="" />
        <a
          href="javascript:;"
          class="tools-link"
          @click="linkToUrl(item.linkUrl)"
          ><span>{{ item.pluginDescriptor }}</span>
          <template>
            <el-tag
              v-for="(tag, index) in item.tags"
              :key="index"
              :type="tag.type"
              effect="plain"
              size="small"
            >
              {{ tag.text }}
            </el-tag>
          </template>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
import moreToolsDataJson from "../../../public/datajson/moreToolsDataJson.json";
export default {
  data() {
    return {
      moreToolsData: [],
    };
  },
  async created() {
    let { zbaseConfig } = await chrome.storage.local.get(
      ['zbaseConfig']
    )
    console.log(zbaseConfig);
    // 如果返回正常数据，则展示服务器数据
    if (zbaseConfig) {
      const data = zbaseConfig.data.config.filter((item) => {
        return item.name === 'moreTools'
      })
      if (data && data.length != 0 && data[0].params) {
        this.moreToolsData = data[0].params
      } else {
        this.moreToolsData = moreToolsDataJson
      }
      this.sortData();
    } else {
      // 展示静态数据
      this.moreToolsData = moreToolsDataJson
      this.sortData();
    }
  },
  methods: {
    // 对展示的数据排序，按照id从小到大排序
    sortData() {
      this.moreToolsData.sort((item1, item2) => {
        if (item1.id > item2.id) {
          return 1;
        } else {
          return -1;
        }
      });
    },
    /**
     * 发送消息给BG，Bg进行创建标签
     * @param {*} url 需要跳转的地址
     */
    linkToUrl(url) {
      chrome.runtime.sendMessage({ type: "url", data: url }, function () {});
    },
    /**
     *
     * @param {*} url url请求接口
     * @param {*} bodyData 请求数据
     */
    async getMoreToolsData(url, bodyData) {
      return await new Promise((resolve) => {
        fetch(url, {
          method: "POST",
          body: bodyData,
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
          },
        }).then((response) => resolve(response.json()));
      });
    },
  },
};
</script>

